import '../styles/index.scss';


document.addEventListener('DOMContentLoaded', function(){ 
    const sectionPreload = document.querySelector('.preloadW');
    const rect = document.querySelector('.preloadW__rect');
    const preloadSVG = document.querySelector('.preloadW__svg');
    const W = document.getElementById('Page-1');
    W.style.transform = 'translate(32px)';
    rect.classList.add('preloadW__translated');
        setTimeout(() => {
            rect.style.display = 'none';
            preloadSVG.classList.add('preloadW__svg-scaled');
        }, 3500);
    
    setTimeout(() => {
        sectionPreload.classList.add('preloadW__opacity');
    }, 4700)

    setTimeout(() => {
        sectionPreload.style.display = 'none';
    }, 5200)

    let tl = new TimelineLite();
    tl.delay(5.3)
    tl
    .fromTo(document.querySelector('.header1__actual-capture') ,0.5, {y: -10}, {y: 0})
    .fromTo(document.querySelector('.header1__actual img') ,0.2, {scale: 0}, {scale: 1}, "-=0.5")
    .fromTo(document.querySelector('.header1__nav-link:nth-child(1)') , 0.5, {y: -10 , opacity: 0}, {y: 0, opacity: 1}, "-=0.2")
    .fromTo(document.querySelector('.header1__nav-link:nth-child(2)') , 0.5, {y: -10 , opacity: 0}, {y: 0, opacity: 1}, "-=0.2")
    .fromTo(document.querySelector('.header1__nav-link:nth-child(3)') , 0.5, {y: -10 , opacity: 0}, {y: 0, opacity: 1}, "-=0.2")
    .fromTo(document.querySelector('.header1__nav-link:nth-child(4)') , 0.5, {y: -10 , opacity: 0}, {y: 0, opacity: 1}, "-=0.2")
    .fromTo(document.querySelector('.header1__nav-link:nth-child(5)') , 0.5, {y: -10 , opacity: 0}, {y: 0, opacity: 1}, "-=0.2")
});
